.form {
  display: flex;
  align-items: center;
}

.input {
  border: 0px solid #fff;
  border-bottom: 1px solid #fff;
  padding: 5px 15px;
  background: transparent;
  margin-right: 25px;
  padding-left: 0;
  outline: none;
  color: #fff;
}

.input:focus {
  border: 0px solid #fff;
  border-bottom: 1px solid #fff;
}

.submit {
  padding: 5px 15px;
  border: 0px solid black;
  background: #fff;
  cursor: pointer;
  color: #000;
  font-size: 16px;
}
