.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 1001;
}

.active {
  display: flex;
}

.modal__container {
  min-width: 300px;
  padding: 20px 15px;
  border-radius: 15px;
  color: #000;
  height: auto;
  background: #fff;
}
